import React, { useContext, useState,useMemo } from "react";
import s from "./index.module.css";
import cn from "classnames";
import { calcDiscountPrice, isLiked, createMarkup} from "../../utils/product";
import {ReactComponent as Save} from "./img/save.svg";
import truck from './img/truck.svg';
import quality from './img/quality.svg';
//import { useNavigate } from "react-router-dom";
import { UserContext } from "../../Context/userContext";
import { ContentHeader } from '../../components/ContentHeader/ContentHeader';
import { Rating } from '../../components/Rating/Rating';
import { FormReview } from '../FormReview/FormReview';

export const Product = ({ onProductLike, pictures, likes=[], revievs=[], tags, name,price,discount,description, weight, _id, setProduct}) => {
  const user = useContext(UserContext);
 /*  const [rating, setRating] = useState(3);
  const navigate = useNavigate(); */
  const discount__price = calcDiscountPrice(price, discount);
  const isLike = isLiked(likes, user?.user?._id)
  const desctiptionHTML = createMarkup(description);

  const ratingCount = useMemo(() => Math.round(revievs.reduce((acc, r) => acc = acc + r.rating, 0)/revievs.length), [revievs])

  return (
  <>
    <ContentHeader title={name}>
      <div>
        <span></span>: {_id}
        <Rating rating={ratingCount}/> 
        <span>{revievs.length || ""} </span>
      </div>      
    </ContentHeader>

    <div className={s.product}>
      <div className={s.imgWrapper}>
        <img src={pictures} alt={` ${name}`} />
      </div>
      <div className={s.desc}>
        <span className={discount ? s.oldPrice : s.Price}>{price}&nbsp;.</span>

        {discount !== 0 && <span className={cn(s.price, 'card__price_type_discount')}>{discount__price}&nbsp;.</span>}

        <div className={s.btnWrap}>
          <div className={s.left}>
            <button className={s.minus}>-</button>
            <span className={s.num}>0</span>
            <button className={s.plus}>+</button>
          </div>
          <a href="/#" className={(cn("btn", "btn_type_primary", s.cart))}> </a>
        </div>
        <button className={cn(s.like, {[s.likeActive]: isLike})}  onClick={onProductLike}>
          <Save/>
        <span>{isLike ? ' ' : ' '}</span>
        </button>

        <div className={s.delivery}>
          <img src={truck} alt="truck" />
          <div className={s.right}>
            <h3 className={s.name}>   !</h3>
            <p className={s.text}>  <span className={s.bold}> 399 </span></p>
          </div>
        </div>

        <div className={s.delivery}>
          <img src={quality} alt="quality" />
          <div className={s.right}>
            <h3 className={s.name}>   !</h3>
            <p className={s.text}>  <span className={s.bold}> 399 </span></p>
          </div>
        </div>
      </div>
    </div>

    <div className={s.box}>
      <h2 className={s.title}></h2>
      <p className={s.subtitle} dangerouslySetInnerHTML={desctiptionHTML}></p> 
      <h2 className={s.title}></h2>
    	<div className={s.grid}>
					<div className={s.naming}></div>
					<div className={s.description}>1  120-200 </div>
					<div className={s.naming}></div>
					<div className={s.description}>490   100 </div>
					<div className={s.naming}></div>
					<div className={s.description}>
						<p>            .</p>
						<p>     .</p>
						<p>  ,      .</p>
						<p>   ,      ,     .</p>
						<p>    .</p>
					</div>
				</div>
			</div>
    
    <ul>
      {revievs.map(reviewData => 
        <li key = {reviewData._id}>{reviewData.text}
          <Rating rating = {reviewData.rating}/>
        </li>
      )}
    </ul>
    
    <FormReview title = {`   ${name}`}  
      productId = {_id} 
      setProduct= { setProduct }/>

  </>

  )
}